import React, { useState, useEffect } from 'react';
import { getProjectList, deleteProject } from 'services/project';
import { Project } from 'typings/project';
import { RouteComponentProps } from 'react-router-dom';
import { Icon, message, Popconfirm } from 'antd';

interface Props extends RouteComponentProps {
  setCurId: React.Dispatch<React.SetStateAction<string>>;
}
function ProjectList(props: Props) {
  const [projectList, setProjectList] = useState<Project[]>([]);

  const goDetail = (id: string) => {
    props.history.push(`/project/${id}`);
  };

  const getAllProject = async () => {
    try {
      const rs = await getProjectList();
      if (rs) {
        setProjectList(rs);
        props.setCurId(rs[0].projectId);
      }
    } catch (error) {
      console.log(error);
      setProjectList([]);
    }
  };

  const delProj = async (projectId: string) => {
    if (!projectId) return;
    try {
      const param = {
        projectId,
      };
      const rs = await deleteProject(param);
      if (rs) {
        getAllProject();
        message.success('Âà†Èô§ÊàêÂäü');
      }
    } catch (error) {
      console.log(error);
      message.success(`Âà†Èô§Â§±Ë¥•: ${error}`);
    }
  };

  // ÂèñÊ∂àÂà†Èô§
  const cancel = (e: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => {
    console.log(e);
  };

  useEffect(() => {
    getAllProject();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="project-list">
      {projectList.map((item: Project) => (
        <div
          key={item.name}
          className="project-list-item"
          onClick={() => {
            props.setCurId(item.projectId);
          }}
        >
          <h2>{item.name}</h2>
          <span className="project-tit">È°πÁõÆÊèèËø∞</span>
          <p className="project-desc">{item.desc}</p>
          {/* <span className="project-tit">È°πÁõÆË∑ØÂæÑ</span>
          <p className="project-url">{item.baseUrl}</p>
          <span className="project-tit">‰ª£ÁêÜË∑ØÂæÑ</span>
          <p className="project-proxy-url">
            {item.proxy.target || 'ÊÇ®ËøòÊ≤°ÊúâËÆæÁΩÆÂì¶ÔΩû'}
          </p> */}
          <Popconfirm
            title="Are you sure delete this project?"
            onConfirm={() => {
              delProj(item.projectId);
            }}
            onCancel={cancel}
            okText="Yes"
            cancelText="No"
          >
            <Icon title="Âà†Èô§" className="delete-icon" type="delete" />
          </Popconfirm>
        </div>
      ))}
    </div>
  );
}

export default ProjectList;
